import "./styles.css";
import { useState, useEffect } from "react";
import Lights from "./Lights";

function App() {
  const colors = {
    red: {
      bgcolor: "red",
      text: "Stop"
    },
    yellow: {
      bgcolor: "yellow",
      text: "Ready"
    },
    green: {
      bgcolor: "green",
      text: "Go"
    },
    grey: {
      bgcolor: "grey"
    }
  };
  const [state, setState] = useState({
    red: colors.red,
    yellow: colors.grey,
    green: colors.grey,
    next: "yellow"
  });

  function handleLightChange() {
    switch (state.next) {
      case "red":
        setState({
          red: colors.red,
          yellow: colors.grey,
          green: colors.grey,
          next: "yellow"
        });
        break;
      case "yellow":
        setState({
          red: colors.grey,
          yellow: colors.yellow,
          green: colors.grey,
          next: "green"
        });
        break;
      case "green":
        setState({
          red: colors.grey,
          yellow: colors.grey,
          green: colors.green,
          next: "yellow2"
        });
        break;
      case "yellow2":
        setState({
          red: colors.grey,
          yellow: colors.yellow,
          green: colors.grey,
          next: "red"
        });
        break;
      default:
        setState({
          red: colors.red,
          yellow: colors.grey,
          green: colors.grey,
          next: "red"
        });
    }
  }

  useEffect(() => {
    setInterval(() => {
      handleLightChange();
    }, 10000);
  });

  return (
    <div className="App">
      <Lights color={state.red} text={colors.red.text} />
      <Lights color={state.yellow} text={colors.yellow.text} />
      <Lights color={state.green} text={colors.green.text} />
      <button onClick={() => handleLightChange(state)}>Change</button>
    </div>
  );
}
export default App;
